<template>
    <div v-if="ready" style='position:relative;'>
        <form id='form' enctype="multipart/form-data" @submit.prevent="handleSubmit">
            <div class="card">
                <div class="card-header">
                    <strong>Substitude Records</strong>
                </div>    
                <div class="card-body" style='padding-left:0;padding-right:0'>
                    <div class="col-12 flex"> <!--date-->
                        <div class="col-2">
                            <button class="form-control" @click="gen_report">{{ btn_text }}</button>
                        </div>
                        <div class="col-2">
                            <button class="form-control" @click="open_sign_form">Generate Form</button>
                        </div>
                        <!--
                        <div class="col-2 radio-toolbar">
                            <input type="checkbox" id="f6_cb" v-model="exc_f6" @click="invert"/>
                            <label for="f6_cb">Exclude F6</label>
                        </div>
                        -->
                        <div class="col-4"></div>
                        <div class="col-4 flex">
                            <div class="col-2"></div>
                            <div class="col-4" style='padding-top:0.35rem'>
                                <label>Select Date:</label>
                            </div>                                
                            <div class="col-6">
                                <input type="date" class="form-control" v-model="tdy_date" @change="get_record"/>
                            </div>                             
                        </div>
                    </div> <!--date-->
                    <div v-if="dp_textarea" class="col-12" style='margin-top:1rem'>
                        <div class="col-12">
                            <textarea id='msg' class="form-control">{{ msg }}</textarea>
                        </div>                       
                    </div>
                <div class="col-12" style='padding:1rem 0 0 0'>
                    <table class="table table-striped first-td-padding">
                        <thead>
                            <tr>
                                <th>Action</th>
                                <th>Leave</th>
                                <th>Assigned To</th>
                                <th>Day</th>
                                <th>Period</th>
                                <th>Class</th>
                                <th>Location</th>
                                <th>Leave Type</th>
                                <th>Application Type</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-if="list_leng>0" v-for="i in list_leng">
                                <td><div class="del" @click="del_record(id[i-1])">Delete</div></td>
                                <td>{{ leave[i-1] }}</td>
                                <td style='padding-top:0.4rem'>
                                    <select class="form-control select" v-model="assigned_id[i-1]" @change="change_assign(i-1)">
                                        <option :value="sub1_id[i-1]">{{ sub1[i-1] }}</option>
                                        <option :value="sub2_id[i-1]">{{ sub2[i-1] }}</option>
                                        <option :value="sub3_id[i-1]">{{ sub3[i-1] }}</option>
                                        <option :value="sub4_id[i-1]">{{ sub4[i-1] }}</option>
                                        <option :value="sub5_id[i-1]">{{ sub5[i-1] }}</option>
                                        <option :value="sub6_id[i-1]">{{ sub6[i-1] }}</option>
                                        <option value="LSS1">Substitute Teacher #1</option>
                                        <option value="LSS2">Substitute Teacher #2</option>
                                    </select>
                                </td>
                                <td style='text-align:center'>{{ day[i-1] }}</td>
                                <td>{{ (parseInt(session[i-1])+1) }}</td>
                                <td>{{ cls[i-1] }}</td>
                                <td>{{ loc[i-1] }}</td>
                                <td>{{ leave_type[i-1] }}</td>
                                <td>{{ app_type[i-1] }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                </div>
            </div>
        </form>     
    </div>   
</template>
<script>
export default {
    data(){
        return{
            exc_f6:false,
            btn_text:'Generate Message',
            dp_textarea:false,
            msg:'',
            id:[0],
            sub1:[''],
            sub1_id:[''],
            sub2:[''],
            sub2_id:[''],
            sub3:[''],
            sub3_id:[''],
            sub4:[''],
            sub4_id:[''],
            sub5:[''],
            sub5_id:[''],
            sub6:[''],
            sub6_id:[''],
            assigned_id:[''],
            day:[0],
            session:[0],
            cls:[''],
            loc:[''],
            leave_type:[''],
            app_type:[''],
            ready:false,
            tdy_date:'',
            ret_list:[''],
            leave:[''],
            assign:[''],
            list_leng:0,
        }
    },   
    async created(){
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.tdy_date = today;
        await this.get_record();
        this.ready = true;
    },
    async mounted(){
        
    },
    methods:{
        invert(){
            this.exc_f6 = !this.exc_f6;
        },
        async gen_report(){
            var formData = new FormData();
            formData.append('date',this.tdy_date);
            await axios.post('subsitude/admin/backend/generate_msg.php',formData)
                .then(
                    (response)=>{
                        this.msg = response.data;
                        this.msg = this.msg.replace(/<br>/g, '\r\n');
                        this.dp_textarea=true;
                        this.btn_text = 'Copy Message';
                        var textarea = document.getElementById("msg");
                        textarea.select();
                        document.execCommand("copy");
                    }
                )
        },
        async change_assign(i){
            this.btn_text='Generate Message';
            this.dp_textarea = false;
            this.msg = '';
            var formData = new FormData();
            formData.append('record_id', this.id[i]);
            formData.append('assign_id', this.assigned_id[i]);
            await axios.post('subsitude/admin/backend/update_assign.php',formData)
                .then(
                    (response)=>{
                        this.$alert(response.data);
                    }
                )
        },
        async get_record(){
            this.btn_text='Generate Message';
            this.dp_textarea = false;
            this.msg = '';
            this.list_leng = this.list_leng-1;
            this.id.slice(0,this.list_leng);
            this.leave.slice(0,this.list_leng);
            this.assigned_id.slice(0,this.list_leng);
            this.assign.slice(0,this.list_leng);
            this.sub1.slice(0,this.list_leng);
            this.sub1_id.slice(0,this.list_leng);
            this.sub2.slice(0,this.list_leng);
            this.sub2_id.slice(0,this.list_leng);
            this.sub3.slice(0,this.list_leng);
            this.sub3_id.slice(0,this.list_leng);
            this.day.slice(0,this.list_leng);
            this.session.slice(0,this.list_leng);
            this.cls.slice(0,this.list_leng);
            this.loc.slice(0,this.list_leng);
            this.leave_type.slice(0,this.list_leng);
            this.app_type.slice(0,this.list_leng);
            this.list_leng = 0;
            var formData = new FormData();
            formData.append('date',this.tdy_date);
            await axios.post('subsitude/admin/backend/fetch_record.php',formData)
                .then
                (
                    (response)=>{
                        if(response.data!='empty'){
                            this.ret_list = response.data;
                            this.list_leng = this.ret_list.length;
                            var i = 0;
                            for(i=0;i<this.ret_list.length;i++){
                                this.id[i] = this.ret_list[i].id;
                                this.leave[i] = this.ret_list[i].leave_name;
                                this.assigned_id[i] = this.ret_list[i].assigned_id;
                                this.assign[i] = this.ret_list[i].assigned_name;
                                this.sub1[i] = this.ret_list[i].sub_name1;
                                this.sub1_id[i] = this.ret_list[i].sub_id1;
                                this.sub2[i] = this.ret_list[i].sub_name2;
                                this.sub2_id[i] = this.ret_list[i].sub_id2;
                                this.sub3[i] = this.ret_list[i].sub_name3;
                                this.sub3_id[i] = this.ret_list[i].sub_id3;
                                this.sub4[i] = this.ret_list[i].sub_name4;
                                this.sub4_id[i] = this.ret_list[i].sub_id4;
                                this.sub5[i] = this.ret_list[i].sub_name5;
                                this.sub5_id[i] = this.ret_list[i].sub_id5;
                                this.sub6[i] = this.ret_list[i].sub_name6;
                                this.sub6_id[i] = this.ret_list[i].sub_id6;
                                this.day[i] = this.ret_list[i].day;
                                this.session[i] = this.ret_list[i].session;
                                this.cls[i] = this.ret_list[i].class;
                                this.loc[i] = this.ret_list[i].location;
                                this.leave_type[i] = this.ret_list[i].type;
                                this.app_type[i] = this.ret_list[i].app_type;
                            }
                        }
                    }                    
                )
                this.$forceUpdate();
        },
        handleSubmit(){
            var formData = new FormData();
        },
        open_sign_form(){
            window.open("src/subsitude/admin/backend/sign_form.php?date="+this.tdy_date);
        },
        re_assign(){
            var formData = new FormData();
            formData.append("date",this.tdy_date);
            axios.post('subsitude/admin/backend/rearrange.php',formData)
                    .then(
                        (response)=>{
                            if(response.data){
                                this.get_record();
                                this.$forceUpdate();
                            }
                        }
                    )
        },
        del_record(id){
            var msg = "Confirm deleting subsititude record?";
            if(confirm(msg)){
                var formData = new FormData();
                formData.append('rid',id);
                axios.post('subsitude/admin/backend/del_record.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='OK'){
                                this.re_assign();
                            }
                        }
                    )
            }
        }
    }
}
</script>
<style scoped>
td{
    transition: all 0.3s ease-in-out;
}
th{
    font-size:0.95rem;
}
.del{
    cursor:pointer;
    text-align:center;
}
.del:hover{
    background-color:rgba(255,0,0,0.5);
    color : #ffffff;
}
.inner_overlay{
    z-index: 5;
    width:65%;
    max-height:60%;
    position:absolute;
    margin-left:auto;
    margin-top:auto;
    margin-bottom:auto;
    margin-right:auto;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color:rgba(255, 255, 255, 0.95);
    box-shadow: 5px 5px 5px 5px rgba(211, 211, 211, 0.6);
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.sub_tr{
    height:5rem!important;
}
</style>




